	module.exports = {"data":{"markdownRemark":{"html":"<p>This week our two farmhand hosts get swept into an enchanted world where magic and topics lurk around every corner. After a pack of wild beasts sent by the Dark One murders their families they set off on a quest where they face five tasks: Instagram, parking tickets, Dan's marketing lab, podcast envy, and finally products that aren’t quite as advertised.</p>\n<p>Once the princess is rescued they turn to go home.</p>\n<p>BUT WAIT!</p>\n<p>What is this?!</p>\n<p>And lo, from the pits of darkness, a dedicated husk of a monster upheaves itself from the depths. His decrepit body sends a wail that strikes fear in the hearts of men and wakes babes lying in their cribs. Behold! The monster wakes. Behold! The destroyer of all that is right! And on his forehead there stands a single word like a profane jape of all that humanity holds dear: “NOAH”</p>\n<p>Can they defeat Noah, release the lands of his curse, and restore the world to what it once was? Listen and find out!</p>\n<!-- excerpt-end -->\n<h4>Show Notes:</h4>\n<ul>\n<li><a href=\"https://www.statista.com/statistics/265773/market-share-of-the-most-popular-social-media-websites-in-the-us/\">Most Popular Social Networks In the US</a></li>\n<li><a href=\"http://www.twobitgeeks.com/\">Two Bit Geeks</a></li>\n<li><a href=\"http://www.rollingstone.com/glixel/news/marvel-vs-capcom-delivers-terrible-infinity-eggs-not-stones-w504168\">Infinity Stones</a></li>\n<li><a href=\"https://www.amazon.com/GRHOSE-Crong-Plush-Pororo-Friends/dp/B00812TJW4\">Crong Plush Poro Poro</a></li>\n</ul>","frontmatter":{"title":"Instagram Sucks, Parking Ticket Protest, Marketing Lab, Podcast Envy, Not as Advertised","number":14}}},"pathContext":{"slug":"/episodes/2017-09-25-episode-014/"}}

